import React, { useState } from "react";
import Logo from "../assets/logo.png";
import { Link } from "react-router-dom";
import ReorderIcon from "@material-ui/icons/Reorder";
import "../styles/Navbar.css";


function Navbar() {
  const [openLinks, setOpenLinks] = useState(false);

  const toggleNavbar = () => {
    setOpenLinks(!openLinks);
  };
  return (
    <div className="navbar">
      
      <div className="leftSide" id={openLinks ? "open" : "close"}>
        <img src={Logo} />
        <Link to="/"> Graphica </Link>
        <div className="hiddenLinks">
          <Link to="/"> Home </Link>
          <Link to="/Menu"> Our Serivces </Link>
          <Link to="/About"> About </Link>
          <Link to="/Contact"> Contact </Link>
        </div>
      </div>
      <div className="rightSide">
        <Link to="/"> Home </Link>
        <Link to="/Menu"> 
        Our Serivces
        <div className="dropdownlinks">
          <Link to="/"> Web development </Link>
          <Link to="/Menu"> Graphic Designing </Link>
          <Link to="/About"> Social Media Handling </Link>
          <Link to="/Contact"> Video Editing </Link>
          <Link to="/Contact"> Performance Marketing </Link>
          <Link to="/Contact"> Training Programmes</Link>

        </div>
        </Link> 
          
        <Link to="/About"> About </Link>
        <Link to="Contact">Contact </Link>
        <button onClick={toggleNavbar}>
          <ReorderIcon />
        </button>
      </div>
    </div>
  );
}

export default Navbar;
